<template>
<div>
    <div v-if="user==null" class="row mt-3 pt-5">
        <img :src="$store.state.url_images+'loading.svg'" class="img-fluid d-flex mx-auto" style="height: 100px">
    </div>
    <div class="col" v-if="user!=null">
        <div class="d-flex justify-content-between mt-3 pt-5">
            <span class="font-weight-bold">Informasi Umum</span>
            <input type="button" value="edit" class="btn btn-sm btn-outline-info">
        </div>
        <ul class="list-unstyled mt-3">
            <li class="border-top py-2">{{user.nama_pelanggan}}</li>
            <li class="border-top py-2">
                <i class="fas fa-map-marker-alt mx-2 text-primary"></i>
                <template v-if="user.alamat!=null">{{user.alamat}}</template>
                <template v-else> Belum ada alamat yang tersimpan </template>
            </li>
            <li class="border-top py-2">
                <i class="far fa-calendar-alt mx-2 text-primary"></i>
                {{user.tanggal_lahir}}
            </li>
        </ul>
        <div class="d-flex justify-content-between">
            <span class="font-weight-bold">Whatsapp & Email</span>
            <input type="button" value="edit" class="btn btn-sm btn-outline-info">
        </div>
        <ul class="list-unstyled mt-3">
            <li class="border-top py-2" >
                <i class="fas fa-mobile-alt mx-2 text-primary"></i>
                {{user.no_wa}}
                <i class="fas fa-check-circle text-info"></i>
            </li>
            <li class="border-top py-2">
                <i class="far fa-envelope mx-2 text-primary"></i>
                {{user.email}}
            </li>
        </ul>
        <div class="d-flex justify-content-between">
            <span class="font-weight-bold">Pengaturan</span>
        </div>
        <ul class="list-unstyled mt-3">
            <li class="border-top py-2">
                <i class="fas fa-people-carry text-primary mx-2"></i>
                Gasan Pian
            </li>
            <li class="border-top py-2">
                <i class="far fa-newspaper text-primary mx-2"></i>
                Artikel & Berita
            </li>
            <li class="border-top py-2">
                <i class="fas fa-key text-primary mx-2"></i>
                Ganti Password
            </li>
        </ul>
        <button class="btn btn-outline-secondary btn-block" @click="logout">
            <i class="fas fa-sign-out-alt text-secondary"></i>
            Logout
        </button>
    </div>
</div>
</template>
<script>
export default {
    name:"Profile",
    data(){
        return {
            user:null
        }
    },
    created(){
        this.axios.get(this.$store.state.url_api + "account", {
            headers : {
                "Authorization": "Bearer " + localStorage.token
            }
        }).then(response => {
            this.user = response.data;
        })
    },
    methods:{
        logout(){
            this.axios.get(this.$store.state.url_api + 'auth/logout', {
                headers : {
                    "Authorization": "Bearer " + localStorage.token
                }
            }).then(() => {
                localStorage.removeItem('token');
                this.$store.dispatch('setIfLogin', false);
                this.$store.commit('SAVE_CART', null);
                this.$router.push({name:'login'});
            })
        }
    }
}
</script>